import React, { useState } from "react";
import "../styles/header.css";
import { Link } from "react-router-dom";

const menuItems = [
  {
    key: "work",
    label: "Work",
    path: "/home#work",
  },
  {
    key: "design-process",
    label: "Design Process",
    path: "/design-process",
  },
  {
    isLogo: true,
  },
  {
    key: "about",
    label: "About me",
    external: true,
    path: "https://drive.google.com/file/d/1XM-53OK0GtA1RsyekCahb2gRTR8OEpR6/view?usp=sharing", // Замените на фактический внешний URL
  },
  {
    key: "resume",
    label: "My Resume",
    external: true,
    path: "https://drive.google.com/file/d/1PePXGlOUydQzssu-PdhhR7g8idFUU6sm/view?usp=sharing",
  },
];

const Header = () => {
  const [showMenu, setShowMenu] = React.useState(false);
  const [activeItem, setActiveItem] = React.useState("");

  return (
    <nav className="navbar navbar-expand-lg navbar-light">
      <span className="container">
        <div className="hidden-brand">
          <a href="/" className="navbar-brand">
            <span className="brand">
              <img className="brand_logo" alt="logo" src="logo.svg" />
            </span>
          </a>
        </div>
        <button
          type="button"
          aria-label="Toggle navigation"
          className="navbar-toggler collapsed"
          onClick={() => setShowMenu(!showMenu)}
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div className={`navbar-collapse collapse ${showMenu ? "show" : ""}`}>
          <div className="navbar-nav">
            {menuItems.map((item, idx) => {
              if (item.isLogo) {
                return (
                  <Link
                    to="/"
                    onClick={() => {
                      setActiveItem("");
                      setShowMenu(false);
                    }}
                    key={idx}
                  >
                    <div className="navbar-brand">
                      <span className="brand">
                        <img className="brand_logo" alt="logo" src="logo.svg" />
                      </span>
                    </div>
                  </Link>
                );
              }
              return (
                <div
                  className={`nav-item ${
                    activeItem === item.key ? "active" : ""
                  }`}
                  key={idx}
                >
                  {item.external ? (
                    <a
                      href={item.path}
                      className="nav-link"
                      target="_blank"
                      rel="noopener noreferrer"
                      onClick={() => {
                        setShowMenu(false);
                        setActiveItem(item.key);
                      }}
                    >
                      {item.label}
                    </a>
                  ) : (
                    <Link
                      to={item.path}
                      className="nav-link"
                      onClick={() => {
                        setShowMenu(false);
                        setActiveItem(item.key);
                      }}
                    >
                      {item.label}
                    </Link>
                  )}
                </div>
              );
            })}
          </div>
        </div>
      </span>
    </nav>
  );
};

export default Header;